	module.exports = {"data":{"wordpressPost":{"title":"Top 10 most read in June: Continuity of care and mortality, visual imagery and clinical measures, experiences of belated treatment of Lyme disease and e-cigarette and nicotine replacement therapy use among smokers","content":"<p>&nbsp;</p>\n<figure id=\"attachment_1289\" style=\"width: 300px\" class=\"wp-caption alignleft\"><img class=\"wp-image-1289 size-medium\" title=\"Girija Kaimal et al. BMJ Open 2018;8:e021448\" src=\"https://blogs.bmj.com/bmjopen/files/2018/07/art-therapy-masks-300x205.jpg\" alt=\"\" width=\"300\" height=\"205\" srcset=\"https://blogs.bmj.com/bmjopen/files/2018/07/art-therapy-masks-300x205.jpg 300w, https://blogs.bmj.com/bmjopen/files/2018/07/art-therapy-masks-768x524.jpg 768w, https://blogs.bmj.com/bmjopen/files/2018/07/art-therapy-masks-1024x699.jpg 1024w, https://blogs.bmj.com/bmjopen/files/2018/07/art-therapy-masks-640x437.jpg 640w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Visual imagery from Girija Kaimal et al. BMJ Open 2018;8:e021448</figcaption></figure>\n<p>Four new entries made it into BMJ Open’s top 10 most read articles in June. Holding on to the top spot is <em>Petrilli et al,</em> with an observational study examining patient preferences for physician attire.</p>\n<p>New in at number two is a systematic review by<em> Gray et al,</em> investigating whether there is a relationship between the receipt of continuity of doctor care and mortality. The researchers extracted and analysed data from observational studies conducted in nine countries with very different cultures and health systems; they demonstrate that increased continuity of care by doctors is associated with lower mortality rates.</p>\n<p>At number three is a new entry by <em>Kaimal at al.</em> The researchers examined the associations between the imagery created in art therapy sessions with standardised measures of clinical symptoms in active-duty military service members with a history of traumatic brain injury, post-traumatic stress symptoms and related psychological health conditions. Their finding indicate that there are patterns of recurring associations between clinical symptoms in the visual imagery created in the art therapy sessions; in particular an association between post-traumatic stress scores and visual depiction of psychological injury was noted.</p>\n<p><em>Hirsch et al,</em> are new in at number five. The researchers conducted a qualitative interview study asking participants about their experience from first Lyme disease symptoms to treatment. The study identified potentially modifiable risk factors for belated treatment of Lyme disease, including symptom misattribution, intermittent symptoms and misperceptions. The final new entry for October is at number ten; <em>Beard et al. </em>report a time-series analysis investigating whether prevalence of e-cigarette and nicotine replacement therapy use among smokers is associated with average cigarette consumption in England. The researchers found no evidence of an association between the rise in use of e-cigarettes and decline in NRT use and changes in cigarette consumption.</p>\n<p>&nbsp;</p>\n<table style=\"height: 946px\" width=\"744\">\n<tbody>\n<tr>\n<td width=\"45\"><strong>Rank</strong></td>\n<td width=\"170\"><strong>Author(s)</strong></td>\n<td width=\"401\"><strong>Title</strong></td>\n</tr>\n<tr>\n<td width=\"45\">1</td>\n<td width=\"170\"><em>Petrilli et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/5/e021239\" target=\"_blank\" rel=\"noopener\">Understanding patient preference for physician attire: a cross-sectional observational study of 10 academic medical centres in the USA</a></td>\n</tr>\n<tr>\n<td width=\"45\">2</td>\n<td width=\"170\"><em>Gray et al. </em></td>\n<td width=\"401\"><a href=\"https://bmjopen.bmj.com/content/8/6/e021161\" target=\"_blank\" rel=\"noopener\">Continuity of care with doctors—a matter of life and death? A systematic review of continuity of care and mortality</a></td>\n</tr>\n<tr>\n<td width=\"45\">3</td>\n<td width=\"170\"><em>Kaimal et al. </em></td>\n<td width=\"401\"><a href=\"https://bmjopen.bmj.com/content/8/6/e021448\" target=\"_blank\" rel=\"noopener\">Observational study of associations between visual imagery and measures of depression, anxiety and post-traumatic stress among active-duty military service members with traumatic brain injury at the Walter Reed National Military Medical Center</a></td>\n</tr>\n<tr>\n<td width=\"45\">4</td>\n<td width=\"170\"><em>Chiavaroli et al.</em></td>\n<td width=\"401\"><a href=\"http://bmjopen.bmj.com/content/8/3/e019438\" target=\"_blank\" rel=\"noopener\">Effect of pasta in the context of low-glycaemic index dietary patterns on body weight and markers of adiposity: a systematic review and meta-analysis of randomised controlled trials in adults</a></td>\n</tr>\n<tr>\n<td width=\"45\">5</td>\n<td width=\"170\"><em>Hirsch et al. </em></td>\n<td width=\"401\"><a href=\"https://bmjopen.bmj.com/content/8/6/e021367\" target=\"_blank\" rel=\"noopener\">Obstacles to diagnosis and treatment of Lyme disease in the USA: a qualitative study</a></td>\n</tr>\n<tr>\n<td width=\"45\">6</td>\n<td width=\"170\"><em>Xie et al.</em></td>\n<td width=\"401\"><a style=\"color: #54397f\" href=\"https://bmjopen.bmj.com/content/7/6/e015735\" target=\"_blank\" rel=\"noopener\">Risk of death among users of Proton Pump Inhibitors: a longitudinal observational cohort study of United States veterans</a></td>\n</tr>\n<tr>\n<td width=\"45\">7</td>\n<td width=\"170\"><em>Hurst et al.</em></td>\n<td width=\"401\"><a style=\"color: #54397f\" href=\"http://bmjopen.bmj.com/content/8/1/e019589\">Effects of changes in eating speed on obesity in patients with diabetes: a secondary analysis of longitudinal health check-up data</a></td>\n</tr>\n<tr>\n<td width=\"45\">8</td>\n<td width=\"170\"><em>Gimson et al. </em></td>\n<td width=\"401\"><a href=\"https://bmjopen.bmj.com/content/8/4/e019399\" target=\"_blank\" rel=\"noopener\">Support for midlife anxiety diagnosis as an independent risk factor for dementia: a systematic review</a></td>\n</tr>\n<tr>\n<td width=\"45\">9</td>\n<td width=\"170\"><em>Papadopoulou et al. </em></td>\n<td width=\"401\"><a href=\"https://bmjopen.bmj.com/content/8/3/e018895\" target=\"_blank\" rel=\"noopener\">Maternal caffeine intake during pregnancy and childhood growth and overweight: results from a large Norwegian prospective observational cohort study</a></td>\n</tr>\n<tr>\n<td width=\"45\">10</td>\n<td width=\"170\"><em>Beard et al.</em></td>\n<td width=\"401\"><a href=\"https://bmjopen.bmj.com/content/8/6/e016046\" target=\"_blank\" rel=\"noopener\">Is prevalence of e-cigarette and nicotine replacement therapy use among smokers associated with average cigarette consumption in England? A time-series analysis</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.6--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"24355948-c480-52ec-8682-8b59b870b10c"}}

